import { useState } from 'react';
import React from 'react';

function FilterableProductTable({ products }) {
  const [filterText, setFilterText] = useState('');
  const [isSearchEnabled, setIsSearchEnabled] = useState(false);

  const handleSearchButtonClick = () => {
    setIsSearchEnabled(true);
  };

  const handleResetButtonClick = () => {
    setIsSearchEnabled(false);
    setFilterText('');
  };

  return (
    <div>
      <SearchBar 
        filterText={filterText} 
        onFilterTextChange={setFilterText}
        isSearchEnabled={isSearchEnabled} />
      <button onClick={handleSearchButtonClick}></button>
      <button onClick={handleResetButtonClick}></button>
      <ProductTable 
        products={products} 
        filterText={filterText}
        isSearchEnabled={isSearchEnabled} />
    </div>
  );
}

function ProductCategoryRow({ category }) {
  return (
    <tr>
      <th colSpan="2">
        {category}
      </th>
    </tr>
  );
}

function ProductRow({ product }) {
  return (
    <tr>
      <td>{product.name}</td>
      <td><a href={product.price} target="_blank" rel="noopener noreferrer">{product.price}</a></td>
    </tr>
  );
}

function ProductTable({ products, filterText, isSearchEnabled }) {
  const rows = [];
  let lastCategory = null;

  if (!isSearchEnabled) {
    // 
    return null;
  }

  if (filterText.trim() === '') {
    // 
    return null;
  }

  products.forEach((product) => {
    if (
      product.name.toLowerCase().indexOf(
        filterText.toLowerCase()
      ) === -1
    ) {
      return;
    }
    if (product.category !== lastCategory) {
      rows.push(
        <ProductCategoryRow
          category={product.category}
          key={product.category} />
      );
    }
    rows.push(
      <ProductRow
        product={product}
        key={product.name} />
    );
    lastCategory = product.category;
  });

  return (
    <table>
      <thead>
        <tr>
          <th></th>
          <th>URL</th>
        </tr>
      </thead>
      <tbody>{rows}</tbody>
    </table>
  );
}

function SearchBar({
  filterText,
  onFilterTextChange,
  isSearchEnabled,
}) {
  return (
    <form>
      <input 
        type="text" 
        value={filterText} placeholder="Search..." 
        onChange={(e) => onFilterTextChange(e.target.value)} 
        disabled={isSearchEnabled} />
    </form>
  );
}

